"use client"

import { useState, useEffect } from "react"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Textarea } from "@/components/ui/textarea"

interface Course {
  id: number;
  title: string;
  instructor_name: string;
  status: string;
}

interface Category {
  id: number;
  name: string;
  description: string;
  created_at: string;
  course_count: number;
  courses: Course[];
}

export default function CategoriesPage() {
  const [categories, setCategories] = useState<Category[]>([])
  const [loading, setLoading] = useState(true)
  const [isAddingCategory, setIsAddingCategory] = useState(false)
  const [searchQuery, setSearchQuery] = useState("")

  const [categoryForm, setCategoryForm] = useState({
    name: "",
    description: ""
  })

  const fetchCategories = async () => {
    try {
      const response = await fetch('/api/categories')
      if (!response.ok) {
        throw new Error('Failed to fetch categories')
      }
      const data = await response.json()
      setCategories(data)
    } catch (error) {
      console.error('Error fetching categories:', error)
    } finally {
      setLoading(false)
    }
  }

  useEffect(() => {
    fetchCategories()
  }, [])

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    try {
      const response = await fetch('/api/categories', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(categoryForm),
      })

      if (!response.ok) {
        throw new Error('Failed to create category')
      }

      fetchCategories() // Refresh the list
      setIsAddingCategory(false)
      setCategoryForm({
        name: "",
        description: ""
      })
    } catch (error) {
      console.error('Error creating category:', error)
    }
  }

  if (loading) {
    return (
      <div className="p-8">
        <div className="animate-pulse">
          <div className="h-8 bg-gray-200 rounded w-1/4 mb-4"></div>
          <div className="h-4 bg-gray-200 rounded w-1/3 mb-8"></div>
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
            {[1, 2, 3].map((i) => (
              <Card key={i} className="p-6">
                <div className="space-y-4">
                  <div className="h-6 bg-gray-200 rounded w-1/3"></div>
                  <div className="h-4 bg-gray-200 rounded w-3/4"></div>
                  <div className="space-y-2">
                    <div className="h-4 bg-gray-200 rounded w-1/2"></div>
                    <div className="h-4 bg-gray-200 rounded w-1/3"></div>
                  </div>
                </div>
              </Card>
            ))}
          </div>
        </div>
      </div>
    )
  }

  const filteredCategories = categories.filter(category =>
    category.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
    category.description.toLowerCase().includes(searchQuery.toLowerCase())
  )

  return (
    <div className="p-8">
      <div className="flex justify-between items-center mb-8">
        <div>
          <h1 className="text-2xl font-bold mb-2">Course Categories</h1>
          <p className="text-gray-600">Manage and organize course categories</p>
        </div>
        <Button onClick={() => setIsAddingCategory(true)}>Add Category</Button>
      </div>

      <div className="mb-6">
        <Input
          placeholder="Search categories..."
          value={searchQuery}
          onChange={(e) => setSearchQuery(e.target.value)}
          className="max-w-sm"
        />
      </div>

      {/* Add Category Form */}
      {isAddingCategory && (
        <Card className="p-6 mb-8">
          <h2 className="text-xl font-bold mb-6">Add New Category</h2>
          <form onSubmit={handleSubmit} className="space-y-6">
            <div className="space-y-2">
              <Label htmlFor="name">Category Name</Label>
              <Input
                id="name"
                value={categoryForm.name}
                onChange={(e) => setCategoryForm(prev => ({ ...prev, name: e.target.value }))}
                required
              />
            </div>

            <div className="space-y-2">
              <Label htmlFor="description">Description</Label>
              <Textarea
                id="description"
                value={categoryForm.description}
                onChange={(e) => setCategoryForm(prev => ({ ...prev, description: e.target.value }))}
                required
              />
            </div>

            <div className="flex gap-4">
              <Button type="submit">Add Category</Button>
              <Button
                type="button"
                variant="outline"
                onClick={() => setIsAddingCategory(false)}
              >
                Cancel
              </Button>
            </div>
          </form>
        </Card>
      )}

      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
        {filteredCategories.map((category) => (
          <Card key={category.id} className="p-6">
            <div className="mb-4">
              <h3 className="text-lg font-semibold">{category.name}</h3>
              <p className="text-sm text-gray-500 mt-1">{category.description}</p>
            </div>

            <div className="space-y-3 mb-6">
              <div className="flex justify-between text-sm">
                <span className="text-gray-500">Total Courses</span>
                <span className="font-medium">{category.course_count}</span>
              </div>
              <div className="flex justify-between text-sm">
                <span className="text-gray-500">Created</span>
                <span className="font-medium">
                  {new Date(category.created_at).toLocaleDateString()}
                </span>
              </div>
            </div>

            {category.courses.length > 0 && (
              <div className="mb-6">
                <h4 className="text-sm font-medium mb-2">Recent Courses</h4>
                <div className="space-y-2">
                  {category.courses.slice(0, 3).map((course) => (
                    <div
                      key={course.id}
                      className="text-sm p-2 bg-gray-50 rounded-lg"
                    >
                      <p className="font-medium">{course.title}</p>
                      <p className="text-gray-500">{course.instructor_name}</p>
                    </div>
                  ))}
                </div>
              </div>
            )}

            <div className="flex justify-end gap-3">
              <Button variant="outline">View Details</Button>
              <Button>Edit</Button>
            </div>
          </Card>
        ))}
      </div>

      {filteredCategories.length === 0 && (
        <Card className="p-12 text-center">
          <p className="text-lg text-gray-500">No categories found</p>
        </Card>
      )}
    </div>
  )
}
